

.navbarBg{    
  z-index: 9;
  position: sticky;
  top:0;
  transition: all 0.5s ease-in-out;
  height: 0!important;
  margin-top: -85px;
  border-bottom: 85px solid rgba(0, 0, 0, 0.712);;

  margin-top: -85px;
border-bottom: 85px solid #08071B; 
  filter: blur(20px) opacity(0%);
}

.navbarBg.show{
    filter: blur(20px) opacity(70%);
}

.sideBarOpened{
  opacity: .5;
  height:100%;
  overflow: hidden!important;
}
.no-scroll {
  overflow: hidden;
}